@import "PriceImpactGraph.css";

.price-impact-warning .content {
  height: 100%;

  font-family: Montserrat, sans-serif;
  font-weight: 500;
  font-size: 14px;

  color: #5B5B5C;
}

.price-impact-warning .container {
  width: 100%;

  display: -ms-inline-flexbox;

  display: inline-flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

  -ms-flex-direction: column;

      flex-direction: column;

  border: 1px solid #F7F7F8;
  border-radius: 4px;

  margin-top: auto;
  margin-bottom: auto;

  padding: 42px 32px;
}

.price-impact-warning .impact-text {
  color: #5B5B5C;
  letter-spacing: 1px;
  text-align: center;

  margin: 16px 0;
}

.price-impact-warning .continue-text {
  font-weight: 600;
  letter-spacing: 1px;
  text-align: center;

  margin: 0;
}

.price-impact-warning .impact-text .danger {
  font-weight: 600;
  color: #E53935;
}


